import { FC } from "react";
import { useNavigate } from "react-router-dom";
import { motion } from "framer-motion";

import { gameStore } from "store/game";

import Paths from "const/paths";

import {
  StartContainer,
  StartTextContainer,
  StartText,
  StartButtonContainer,
  StartButton,
} from "./Start.styled";

const Start: FC<{}> = (): JSX.Element => {
  const navigation = useNavigate();

  const { setStartGame } = gameStore();

  const onStartGameButtonClick = (): void => {
    setStartGame();
    navigation(Paths.GamePage, { replace: true });
  };

  return (
    <StartContainer>
      <motion.div
        initial={{ x: -150, opacity: 0 }}
        animate={{ x: 0, opacity: 1 }}
        transition={{ ease: "linear", duration: 1 }}
      >
        <StartTextContainer>
          <motion.div
            initial={{ x: -150, opacity: 0 }}
            animate={{ x: 0, opacity: 1 }}
            transition={{ ease: "linear", delay: 1, duration: 1 }}
          >
            <StartText>
                -     
                   .
            </StartText>
          </motion.div>

          <motion.div
            initial={{ x: -150, opacity: 0 }}
            animate={{ x: 0, opacity: 1 }}
            transition={{ ease: "linear", delay: 1, duration: 1.5 }}
          >
            <StartText>
                     
              .
            </StartText>
          </motion.div>

          <motion.div
            initial={{ x: -150, opacity: 0 }}
            animate={{ x: 0, opacity: 1 }}
            transition={{ ease: "linear", delay: 1, duration: 2 }}
          >
            <StartText>
                   ,     . 
                   ,   
               .
            </StartText>
          </motion.div>
        </StartTextContainer>

        <StartButtonContainer>
          <motion.div
            initial={{ x: -150, opacity: 0 }}
            animate={{ x: 0, opacity: 1 }}
            transition={{ ease: "linear", delay: 2, duration: 2 }}
          >
            <StartButton
              onClick={onStartGameButtonClick}
              type="button"
              name=" "
            >
               
            </StartButton>
          </motion.div>
        </StartButtonContainer>
      </motion.div>
    </StartContainer>
  );
};

export default Start;
